*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  width: 100%;
}

body {
  max-width: 1366px;
  min-width: $mobile-width;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
